	//[20140521][jinrifeng](pLaizi)
	//2  bDivide3ex
	//pcArraypoint-countpointcount ex 
	_findMinDouble: function(pLaizi, pcArray, count, ex, bDivide) {			//pcArray 
		ddz.LOGD(this._TAG, "[_findMinDouble][in]");
		//pcArray.sort(this._sortByPoint);	//
		for(var i = 0; i < pcArray.length; i++){
			var ele = pcArray[i];
			ddz.LOGD(this._TAG, ""+ele.count);
			// ddz.CardTable.print(ele.cards);
		}
        var newarr = pcArray.slice(0);
        var returnArr = [];
		var laizi_use = 0;
		for (var i = 0; i < newarr.length; i++) {
			var t = newarr[i];
			if (t.count != 2 || (bDivide && hall.GlobalFuncs.FindInArray(ex, t.point) >= 0) || t.point == 15) { //ex
				continue;
			}
			returnArr.push([t.cards[0], t.cards[1]]);
			if (returnArr.length >= count) {
				ddz.LOGD(this._TAG, "[_findMinDouble][renturn][]");
				return returnArr;
			}
		}


        //
       if (pLaizi != null && pLaizi.count > 0) {
           for (var i = 0; i < newarr.length; i++) {
               var t = newarr[i];
               if (t.point != 15 && t.count ==1 && (hall.GlobalFuncs.FindInArray(ex, t.point) < 0 || !bDivide)) { //
                   returnArr.push([t.cards[0],ddz.GlobalFuncs.getLaiziNumByValue(t.point)]);
                   laizi_use++;
                   if (returnArr.length >= count){
                   	   pLaizi.count -= laizi_use;  //
                       ddz.LOGD(this._TAG, "[_findMinDouble][renturn][]");
                       return returnArr;
                   }
                   if (laizi_use >= pLaizi.count) 
                   		break;
                }
            }
        }

		if (bDivide) { //3
			for (var i = 0; i < newarr.length; i++) { //3, 
				var t = newarr[i];
				if (t.count == 3 && hall.GlobalFuncs.FindInArray(ex, t.point) < 0) {
					ddz.LOGD(this._TAG, "split 3 cards in find min double");
					var cards = t.cards;
					returnArr.push([cards[0], cards[1]]);
					if (returnArr.length >= count) {
						pLaizi.count -= laizi_use;  //
						ddz.LOGD(this._TAG, "[_findMinDouble][renturn][3]");
						return returnArr;
					}
				}
			}
		}

                                   
        //
        if (pLaizi != null && pLaizi.count - laizi_use > 1) {
           returnArr.push([pLaizi.cards[0],pLaizi.cards[1]]);
           laizi_use += 2;
           if (returnArr.length >= count) {
           		pLaizi.count -= laizi_use;  //
           	 	ddz.LOGD(this._TAG, "[_findMinDouble][renturn][2]");
           		return returnArr;
		    }
        }
                                   
		ddz.LOGD(this._TAG, "[_findMinDouble][out][]");
		return []; //null
	},

	_findBomb: function(pcArray, lz_obj) {
		ddz.LOGD(this._TAG, "start find bomb ....");
		var return_arr = [];
		
		for(var laiziMax = 0; laiziMax <= lz_obj.count; laiziMax++){
			ddz.LOGD('' + laiziMax + '');
			var newarr = this._fillDicarr(pcArray);
			for (var i = newarr.length - 1; i >= 2; i--) { //
				var t = newarr[i];
				var need = 4 - t.count;
				if ((need == laiziMax) && (need < 4) && (need <= lz_obj.count)) {	
					var cards = t.cards.slice(0);
					var n = ddz.GlobalFuncs.getLaiziNumByValue(t.point);
					for(var j = 0; j < need; j++) {
						cards.push(n);
					}
					return_arr.push(cards);
				}
			}
		}
		
		// 
		if(lz_obj.count == 4) {
			var c = lz_obj.cards[0];
			return_arr.push([c, c, c, c]);
		}
		ddz.LOGD(this._TAG, "find bomb length " + return_arr.length);
		return return_arr;
	},

	_findRocket: function(pcArray) {
		ddz.LOGD(this._TAG, "start find rocket ....");
		var bking = false;
		var rking = false;
		for (var i = pcArray.length - 1; i >= 0; i--) { //
			var t = pcArray[i];
			if (t.point == 14) {
				rking = true;
			} 
			else if (t.point == 13) {
				bking = true;
			}
			if (rking && bking) { //break
				break;
			}

		}
		var ret = bking&&rking;
		if (ret) {
			ddz.LOGD(this._TAG, "");
		}
		else{
			ddz.LOGD(this._TAG, "");
		}
		return ret;
	},

